'use strict';

window.onload = function () {
    // hamburger
    const hamburger = document.querySelector('.header-hamburger'),
          menu = document.querySelector('.header-user'),
          logo = document.querySelector('.header-home');

    menu.preventDefault;
    hamburger.preventDefault;

    hamburger.addEventListener('click', function(e){
        menu.classList.toggle('menu-show');
        hamburger.classList.toggle('hamburger_active');
        logo.classList.toggle('logo_active');
    });
    welcome_screen.addEventListener('click', function(e){
        menu.classList.remove('menu-show');
        hamburger.classList.remove('hamburger_active');
        logo.classList.remove('logo_active');
    });


    const moduleBank = (function () {
        let value = 0,
            listValNum = [],
            listValStr= [];

        return {
            init: function () {
                // init value from database JSON
            },
            addValue: function (val) {
                value += val;
                listValNum.push(val);
            },
            reduceValue: function (val) {
                value -= val;
                listValNum.push(-val);
            },
            getValue: function () {
                return value;
            },
            addString: function (val) {
                listValStr.push(val);
            },
            getListValNum: function () {
                return listValNum;
            },
            getListValStr: function () {
                return listValStr;
            }
        }
    })();

    const createElement = function (type, className, inner) {
        const el = document.createElement(type);
        el.classList.add(className);
        el.textContent = inner;
        return el;
    };

    const createItemHistory = function (textValue, scoreValue, sign = 1) {
        if (sign === true) {
            scoreValue = '+ ' + scoreValue;
        } else {
            scoreValue = '- ' + scoreValue;
        }

        const itemHistory = document.createElement('div');
        itemHistory.classList.add('user_data-item');

        let date = new Date();
        let time = date.getHours() + ':' + date.getMinutes();
        const scoreEl = createElement('div', 'user_data__score', scoreValue),
              textEl = createElement('div', 'user_data__text', textValue),
              dateEl = createElement('div', 'user_data__date', time);

        itemHistory.appendChild(scoreEl);
        itemHistory.appendChild(textEl);
        itemHistory.appendChild(dateEl);
        
        return itemHistory;
    };

    const writeScore = function (currentScore) {
        const scoreEl = document.querySelector('.user_data-profile__score_big');
        scoreEl.textContent = currentScore;
    };

    const eventSubmit = function () {
        
        // check inputs TEXT
        const inputsText = document.querySelectorAll('.form-input_text');
        let textValue = inputsText[0].value,
            scoreValue = +inputsText[1].value;

        for (let i = 0; i < inputsText.length; i++) {
            if (!inputIsNotEmpty(inputsText[i])) {
                alert ('Пустая строка');
                return;
            }
        }

        if (typeof textValue != 'string' || isNaN(scoreValue)) {
            alert ('Неправильные типы данных');
            return;
        }

        // производим действия
        const inputsRadio = document.querySelectorAll('.form__input_radio');
        const containerHistory = document.querySelector('.user_data-container');
        if (inputsRadio[0].checked) {
            moduleBank.addValue(scoreValue);
            containerHistory.appendChild(createItemHistory(textValue, scoreValue, true));
        } else {
            moduleBank.reduceValue(scoreValue);
            containerHistory.appendChild(createItemHistory(textValue, scoreValue, false));
        }

        writeScore(moduleBank.getValue());

        moduleBank.addString(textValue);
    }

    const inputIsNotEmpty = function (input) {
        return (input.value.length > 0) ? true : false;
    };

    const check = function (input) {
        return (input.checked) ? true : false;
    };
    
    writeScore(moduleBank.getValue());
    const btnSubmit = document.querySelector('.form__submit');
    btnSubmit.addEventListener('mouseover', eventSubmit);
};
